import React, { Component } from "react";
import { Route, Link, BrowserRouter as Router } from "react-router-dom";
import { connect } from "react-redux";

const BASE_URL = "https://murmuring-atoll-51852.herokuapp.com/api/v2";
export class ListCandidates extends Component {
  constructor(props) {
    super(props);
    this.state = {
      candidates: []
    };
  }

  render() {
    const { candidates } = this.props;
    console.log(this.props)
    return (
      <div data-test="form-app">
        <select>
          {candidates.map(candidate => (
            <React.Fragment key={candidate.id}>
              <option value="" />
              <option value="">
                {candidate.id}.{candidate.candidate}
              </option>
            </React.Fragment>
          ))}
        </select>
      </div>
    );
  }
}
function mapStateToProps(state, ownProps) {
  const { user, candidates } = state;
  console.log(ownProps);

  return { user, candidates: candidates.filter(candidate => candidate.office === ownProps.office_name) };
}
export default connect(mapStateToProps)(ListCandidates);
